<template>
  <div>
    <div class="search_container">
      <el-form :inline="true" :model="params" class="demo-form-inline">
        <el-form-item label="采购组织" label-width="110px" size="mini">
          <el-select clearable v-model="params.company_id" placeholder="请选择" @focus="companyListInvok" @change="companyChange" filterable maxlength="100">
            <el-option v-for="item in companyList" :key="item.company_id" :label="item.company_name" :value="item.company_id">
              <span :class="item.type==1 ? 'optionStyle': '' ">{{ item.company_name }}</span>
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="部门" label-width="110px" size="mini">
          <el-select clearable v-model="params.department_id" placeholder="请选择" @focus="DepartmentListInvok" @change="departmentChange" filterable maxlength="100">
            <el-option :label="item.department_name" :value="item.department_id" :key="item.department_id" v-for="item in departmentList"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="供应商(厂家)" label-width="110px" size="mini">
          <el-select clearable v-model="params.supplier_id" maxlength="100" @change="supplierChangesave" :filter-method="supplierListInvok" @focus="supplierListInvok" filterable placeholder="请选择">
            <el-option v-for="item in supplierList" :label="item.supplier_name" :value="item.supplier_id" :key="item.supplier_id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="业务日期" label-width="110px" size="mini">
          <el-date-picker v-model="timeValue" type="daterange" @change="changeDate" range-separator="-" value-format="yyyy-MM-dd" start-placeholder="开始日期" end-placeholder="结束日期">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="单据号" label-width="110px" size="mini">
          <el-input v-model="params.rebate_sn" maxlength="100" width="200px" placeholder="请输入"></el-input>
        </el-form-item>
        <el-form-item label="返利类型" label-width="110px" size="mini">
          <el-select clearable v-model="params.rebate_type_id" @change="rebateTypeChange" @focus="RebateTypeListInvok">
            <el-option v-for="item in rebateType" :label="item.rebate_type_name" :value="item.rebate_type_id" :key="item.rebate_type_id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="返利周期" label-width="110px" size="mini">
          <el-select clearable v-model="params.cycle_id" @change="rebateCyclesave" @focus="RebatecycleListInvok">
            <el-option v-for="item in rebatecycle" :label="item.cycle_name" :value="item.cycle_id" :key="item.cycle_id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="品牌" size="mini" label-width="110px">
          <getBrand v-model='params.brand_id' :fatherParams='params' />
        </el-form-item>
        <el-form-item label="审核状态" label-width="110px" size="mini">
          <el-select clearable v-model="params.auditor_status" width="200px">
            <el-option label="全部" :value="0">全部</el-option>
            <el-option label="未审核" :value="1">未审核</el-option>
            <el-option label="已审核" :value="2">已审核</el-option>
            <el-option label="作废" :value="3">作废</el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="审核日期" size="mini">
          <el-date-picker v-model="timeValue1" type="daterange" range-separator="-" value-format="yyyy-MM-dd" start-placeholder="开始日期" end-placeholder="结束日期" clearable>
          </el-date-picker>
        </el-form-item>
        <el-form-item size="mini" class="search-style search">
          <el-button @click="onSearch" size="mini" type="primary">查询</el-button>
          <el-button plain @click="onReset" size="mini">重置</el-button>
        </el-form-item>
      </el-form>
      <div class="add_delet" v-if='is_company_service != 1'>
        <el-button icon="el-icon-circle-plus-outline" type="primary" class="addBtn" size="mini" @click="addFun" v-if="authority.b_add==1">新增</el-button>
        <el-button plain icon="el-icon-delete" size="mini" @click="delFun" v-if="authority.b_del==1">删除</el-button>
        <el-button class="iconfont icon-pass" type="primary" size="mini" @click="examineFun" v-if="authority.b_pass==1">审核</el-button>
        <el-button plain @click="AUDIT_GET" icon="iconfont icon-fanshenhe" v-if="authority.b_nopass==1" size="mini">弃审</el-button>
        <el-button plain class="iconfont icon-zuofei" size="mini" @click="abolishFun" v-if="authority.b_invalid==1">作废</el-button>
      </div>
    </div>
    <div class="table_container">
      <el-table :highlight-current-row="true" tooltip-effect="light" border :data="TableData.tableData" @selection-change="handleSelectionChange" max-height="600" style="width: 100%">
        <el-table-column :show-overflow-tooltip="true" type="selection" fixed="left" align="center" width="44" @selection-change="handleSelectionChange">
        </el-table-column>
        <el-table-column label="操作" fixed="right" width="100">
          <template slot-scope="scope">
            <el-button @click="handleClick(scope.row)" type="text" size="small" v-if="authority.b_info==1">查看</el-button>
            <!-- 服务站不显示按钮 -->
            <span v-if='is_company_service != 1'>
              <el-button @click="updateClick(scope,scope.row)" type="text" size="small" v-if="authority.b_edit==1 && scope.row.auditor_status==1">修改</el-button>
              <el-button @click="updateRow(scope.row)" disabled type="text" size="small" v-if="authority.b_edit==1 && scope.row.auditor_status!=1">
                修改
              </el-button>
            </span>

          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="company_name" label="采购组织" width="200"></el-table-column>

        <el-table-column :show-overflow-tooltip="true" property="rebate_sn" label="单据号" width="200"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="supplier_name" label="供应商(厂家)" width="200"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="brand_name" label="品牌" width="200"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="total_price" label="返利金额" align="right" width="120">
          <template slot-scope="scope">
            <pre>{{scope.row.total_price  | priceFormatNone}}</pre>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="rebate_type_name" label="返利类型" width="200"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="rebate_cycle_name" label="返利周期" width="200"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="business_time" label="业务日期" width="120"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="auditor_status_name" label="审核状态" width="120"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="auditor_name" label="审核人" width="120"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="auditor_time" width="160" label="审核时间">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="add_user_name" label="创建人" width="200"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="add_time" label="单据时间" width="200"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="department_name" label="所属部门" width="100"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="update_user_name" label="修改人" width="200"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="update_time" label="修改时间" width="200"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="remark" label="备注" width="200"></el-table-column>
      </el-table>
    </div>
    <div class="page_container">
      <div class="pageNumber">每页显示</div>
      <el-pagination class="erp-pagination" @size-change="handleSizeChange" :current-page.sync="goPage" :page-size="pageSize1" @current-change="handleCurrentChange" :page-sizes="per_page" layout="total,sizes, prev, pager, next,jumper" prevText="上一页" nextText="下一页" :total="TableData.total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
import { getData, opthionList } from "common/utils/public_function/commonUtils";
import { mapActions, mapGetters } from "vuex";
export default {
  name: "purchaseRebatemanageDefault",
  data() {
    return {
      authority: {},
      searchParams: {},
      timeValue: [],
      loading: false,
      TableData: {
        tableData: [{}],
        total: 10,
      },
      timeValue1: [],
      multipleSelection: [],
      currentPage: 1,
      checkSearch: true,
      checkAdd: true,
      searchRrebateInfo: {},
      checkModify: true,
      modifeRebate: {}, // 修改查看信息
      goPage: 1, //默认搜索页面
      isSearch: true, //检测第几次点击搜索
      list: [], //品牌管理相关
    };
  },
  computed: {
    ...mapGetters(["purchaseDefault_showGet"]),
  },
  methods: {
    ...mapActions(["refundAdd_show", "refundSearch_show", "refundModify_show"]),
    rebateCyclesave(val) {
      if (val) {
        this.params.cycle_name = this.rebatecycle.filter(
          (obj) => obj.cycle_id === val
        )[0].cycle_name;
      }
    },
    rebateTypeChange(val) {
      if (val) {
        this.params.rebate_type_name = this.rebateType.filter(
          (obj) => obj.rebate_type_id === val
        )[0].rebate_type_name;
      }
    },
    supplierChangesave(val) {
      if (val) {
        this.params.supplier_name = this.supplierList.filter(
          (obj) => obj.supplier_id === val
        )[0].supplier_name;
      }
    },
    departmentChange(val) {
      if (val) {
        this.params.department_name = this.departmentList.filter(
          (obj) => obj.department_id === val
        )[0].department_name;
      }
    },
    companyChange(val) {
      if (val != "") {
        this.params.company_name = this.companyList.filter(
          (obj) => obj.company_id === val
        )[0].company_name;
      }
      this.$set(this.params, "supplier_id", "");
      this.$set(this.params, "department_id", "");
    },
    AUDIT_GET() {
      if (this.multipleSelection.length < 1) {
        this.$message({
          message: "请选择需要弃审的信息",
          type: "warning",
        });
        return false;
      }
      var addr = [];
      for (var i = 0; i < this.multipleSelection.length; i++) {
        addr.push(this.multipleSelection[i].rebate_id);
      }
      var rebateArr = addr.toString();
      this.$axMethod
        .auidtorNoPassRebatemanage({
          rebate_id: rebateArr,
        })
        .then((response) => {
          if (response.data.code == 200) {
            this.$message({
              message: response.data.data,
              type: "success",
            });
            getData(this.searchParams); //获取销售订单列表
          }
        });
      //弃审
    },
    examineFun() {
      //审核
      if (this.multipleSelection.length < 1) {
        this.$message({
          message: "请选择需要审核的信息",
          type: "warning",
        });
        return false;
      }
      var addr = [];
      for (var i = 0; i < this.multipleSelection.length; i++) {
        addr.push(this.multipleSelection[i].rebate_id);
      }
      var rebateArr = addr.toString();
      var funcan = {
        examineID: "rebate_id",
        eachData: {
          rebate_id: rebateArr,
        },
        examineApi: "rebateAuditorPass",
        list: {
          UrlApi: "getVenderRebateList",
          data: this.TableData,
          esData: Object.assign(this.searchParams.params, {
            page: this.goPage,
            // company_id:
            //   this.formInline.company_id == undefined
            //     ? Number(localStorage.company_id)
            //     : this.formInline.company_id
          }),
        },
      };
      this.$common.examineFun(funcan);
    },
    abolishFun() {
      //作废
      if (this.multipleSelection.length < 1) {
        this.$message({
          message: "请选择需要作废的信息",
          type: "warning",
        });
        return false;
      }
      var addr = [];
      for (var i = 0; i < this.multipleSelection.length; i++) {
        addr.push(this.multipleSelection[i].rebate_id);
      }
      var rebateArr = addr.toString();

      var funcan = {
        examineID: "rebate_id",
        eachData: {
          rebate_id: rebateArr,
        },
        examineApi: "rebateAuditorInvalid",
        list: {
          UrlApi: "getVenderRebateList",
          data: this.TableData,
          esData: Object.assign(this.searchParams.params, {
            page: this.goPage,
          }),
        },
      };
      this.$common.examineFun(funcan);
    },
    onSearch() {
      //查询
      if (this.timeValue && this.timeValue.length == 2) {
        this.params.start_time = this.timeValue[0];
        this.params.end_time = this.timeValue[1];
      } else {
        this.params.start_time = this.params.end_time = "";
      }
      if (this.timeValue1) {
        this.params.start_auditor_time = this.timeValue1[0];
        this.params.end_auditor_time = this.timeValue1[1];
      } else {
        this.params.start_auditor_time = this.params.end_auditor_time = "";
      }
      this.purchaseDefaultIndexACT({
        goPage: this.goPage,
        params: this.params,
        pageSize: this.pageSize,
      });
      Object.assign(this.searchParams.params, this.params, {
        page: 1,
      });
      getData(this.searchParams);
    },
    onReset() {
      this.$set(this, "params", {});
      this.timeValue = [];
      this.timeValue1 = [];
      this.$set(this, "searchParams", {
        params: this.params, //1默认带的参数
        TableData: this.TableData, //列表数据
        API: "getVenderRebateList", //接口名
        vm: this, //传递的this
      });
      this.onSearch();
    },
    ...mapActions([
      "purchaseDefaultIndexACT",
      "purchaseAdd_show",
      "purchaseSearch_show",
    ]),
    addFun() {
      //新增
      this.$router.push({ path: "/purchase/rebatemanage/rebatemanageAdd" });
      this.purchaseAdd_show({
        // commont: "rebatemanageAdd"
      });
    },
    updateClick(scope, row) {
      //修改
      this.$router.push({ path: "/purchase/rebatemanage/rebatemanageAdd" });
      this.purchaseAdd_show({
        // commont: "rebatemanageAdd",
        rebate_id: row.rebate_id,
        params: this.params,
        goPage: this.goPage,
        pageSize: this.pageSize,
      });
    },
    handleClick(row) {
      //查看
      this.$router.push({ path: "/purchase/rebatemanage/rebatemanageSearch" });
      this.purchaseSearch_show({
        // commont: "rebatemanageSearch",
        rebate_id: row.rebate_id,
        params: this.params,
        goPage: this.goPage,
        pageSize: this.pageSize,
      });
    },
    delFun() {
      //删除
      var deledata = {
        deleID: "rebate_id",
        deleSelect: this.multipleSelection,
        deleApi: "delRebate",
        list: {
          UrlApi: "getVenderRebateList",
          data: this.TableData,
          esData: {
            page: this.goPage,
          },
        },
      };
      this.$common.deletes(deledata, "删除", this);
    },

    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    ievent(...data) {
      this.checkSearch = data[0].checkSearch;
    },
    addEevent(msg) {
      this.checkAdd = msg.checkAdd;
      this.$common.initData("getVenderRebateList", this.TableData);
    },
    changeDate(val) {
      this.params.start_time = val[0];
      this.params.end_time = val[1];
    },
    handleSizeChange(val) {
      Object.assign(this.searchParams.params, {
        per_page: val,
        page: 1,
      });
      getData(this.searchParams);
    },
    handleCurrentChange(val) {
      Object.assign(this.searchParams.params, {
        page: val,
      });
      getData(this.searchParams);
    },
  },
  mounted() {
    this.authority = JSON.parse(localStorage.getItem("authority"));

    if (this.purchaseDefault_showGet && this.purchaseDefault_showGet.params) {
      if (
        this.purchaseDefault_showGet.params.start_time ||
        this.purchaseDefault_showGet.params.start_auditor_time
      ) {
        const { start_time, end_time, start_auditor_time, end_auditor_time } =
          this.purchaseDefault_showGet.params;
        this.timeValue = this.timeValue.concat([start_time, end_time]);
        this.timeValue1 = this.timeValue1.concat([
          start_auditor_time,
          end_auditor_time,
        ]);
      }
      this.$set(this, "params", this.purchaseDefault_showGet.params);
      opthionList(this, this.purchaseDefault_showGet.params);
    }
    if (this.purchaseDefault_showGet.goPage) {
      this.params.page = this.purchaseDefault_showGet.goPage;
      this.params.per_page = this.purchaseDefault_showGet.pageSize;
    }
    this.$set(this, "searchParams", {
      params: this.params, //1默认带的参数
      TableData: this.TableData, //列表数据
      API: "getVenderRebateList", //接口名
      vm: this, //传递的this
    });
    getData(this.searchParams);
  },
};
</script>

<style scoped>
